.footer {
    background-image: url('../img/footer-bg.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: #ccc;
    position: relative;
    color: var(--color-white);

    &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background-color: rgba(50, 50, 50, 0.8);
    }
}

.footer__container {
    padding-top: 65px;
    padding-bottom: 28px;
}

.footer__top-inner {
    display: flex;
}

.footer__top-title {
    position: relative;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-white);
    border-bottom: 2px solid var(--color-white);
    display: inline-block;
    padding-bottom: 3px;
    margin-bottom: 25px;
    z-index: 1;
}

.footer__top-map {
    margin-left: 200px;
}

.footer__top-contacts {
    margin-left: 82px;
}

.footer__list {
    position: relative;
    font-size: 14px;
    line-height: 24px;
}

.footer__item {
    transition: opacity 0.3s;
    &:not(:first-child) {
        margin-top: 5px;
    }

    &:hover {
        transition: opacity 0.3s;
        opacity: 0.7;
    }
}

.social__list {
    position: relative;
    display: flex;
    align-items: center;
}

.social__item {
    &:not(:first-child) {
        margin-right: 25px;
    }
}

.social__link {
    transition: opacity 0.3s;
    svg {
        width: 24px;
        height: 24px;
    }

    &:hover {
        transition: opacity 0.3s;
        opacity: 0.7;
    }
}

.footer__phone {
    display: block;
    font-size: 14px;
    line-height: 24px;
    font-weight: 600;
    padding-right: 36px;
    position: relative;
    background-repeat: no-repeat;
    background-position: right center;
    background-image: url('../img/tel-footer.svg');
    transition: opacity 0.3s;
        
    &:hover {
        transition: opacity 0.3s;
        opacity: 0.7;
    }
}

.footer__email {
    display: block;
    font-size: 14px;
    line-height: 24px;
    font-weight: 600;
    padding-right: 36px;
    position: relative;
    background-repeat: no-repeat;
    background-position: right center;
    background-image: url('../img/email-footer.svg');
    transition: opacity 0.3s;
        
    &:hover {
        transition: opacity 0.3s;
        opacity: 0.7;
    }
}

.footer__email+.footer__phone {
    padding-top: 4px;
}

.footer__bottom {
    position: relative;
    margin: 60px 0 0px;
    border-top: 2px solid rgba(208, 208, 208, 0.2);
}

.copy {
    position: relative;
    display: block;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-alto);
    padding-top: 23px;
    z-index: 1;
}