.consultation {
    position: relative;
    background-image: url('../img/consultation-bg.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    background-color: #ccc;
}

.consultation__container {
    padding-top: 93px;
    padding-bottom: 94px;
}

.consultation__title {
    text-align: center;
    font-weight: 700;
    font-size: 36px;
    line-height: 24px;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
    color: var(--color-white);
}

.form {
    margin-top: 55px;
    display: flex;
    justify-content: space-between;
}

.form-input {
    background: rgba(255, 255, 255, 0.8);
    height: 54px;
    width: 330px;
    border-radius: 13px;
    padding: 0 17px;
    border: none;
    outline: none;
    color: #000;

    &::placeholder {
        color: var(--color-black);
        transition: all 0.3s;
        font-weight: 600;
    }

    &:focus::placeholder {
        color: transparent;
    }
}

.form-btn {
    height: 54px;
    width: 200px;
}