.modal {
	--transition-time: 0.3s;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
	background-color: rgba(0, 0, 0, 0.5);
	cursor: pointer;
	overflow-y: auto;
	overflow-x: hidden;
	text-align: center;
	opacity: 0;
	visibility: hidden;
	transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal::before {
	content: "";
	display: inline-block;
	vertical-align: middle;
	height: 100%;
}

.modal__container {
	position: relative;
	width: 950px;
	min-height: 515px;
	margin: 50px auto;
	display: none;
	vertical-align: middle;
	background-color: #fff;
	border-radius: 13px;
	box-shadow: 2px 2px 20px -2px rgba(0, 0, 0, 0.25);
	cursor: default;
	text-align: right;
}

@media (max-width: 1024px) {
	.modal__container {
		width: 90%;
	}
}

.modal.is-open {
	opacity: 1;
	visibility: visible;
	transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__container.modal-open {
	display: inline-block;
}

.fade {
	opacity: 0;
	transition: opacity var(--transition-time);
}

.fade.animate-open {
	opacity: 1;
	transition: opacity var(--transition-time);
}

.fadeInUp {
	opacity: 0;
	transform: translateY(-100px);
	transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
	opacity: 1;
	transform: translateY(0);
	transition: opacity var(--transition-time), transform var(--transition-time);
}

.disable-scroll {
	position: relative;
	overflow: hidden;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
}

.modal-window {
	margin-top: -23px;
}

.modal-close {
	float: left;
	margin-top: 20px;
	margin-left: 23px;

	svg {
		display: block;
		width: 42px;
		height: 42px;
	}
}

.modal__container {
	border: 3px solid var(--bright-red);
}

.modal-container {
	padding: 40px 75px;
}

.order__name {
	font-weight: 700;
	font-size: 36px;
	line-height: 36px;
	color: var(--bright-red);
	max-width: 710px;
}

.order__title {
	margin-top: 5px;
	font-weight: 600;
	font-size: 36px;
	line-height: 36px;
	color: var(--mine-shaft);
	max-width: 710px;
}

.order__required {
	margin-top: 33px;
	font-weight: 600;
	font-size: 16px;
	line-height: 16px;
	color: var(--mine-shaft);
}

.order__form {
	margin-top: 14px;
}

.order__wrapper {
	max-width: 690px;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.order__input {
	background: rgba(255, 255, 255, 0.8);
	border: 1px solid var(--mine-shaft);
	border-radius: 13px;
	font-weight: 400;
	font-size: 18px;
	line-height: 24px;
	width: 330px;
	height: 54px;
	padding: 0 25px;
	outline: none;
	margin-bottom: 17px;

	&::placeholder {
		font-weight: 600;
		color: var(--color-black);
		transition: all 0.3s;
	}

	&:focus::placeholder {
		color: transparent;
	}
}

.order__btn {
	margin-top: 15px;
	height: 63px;
	width: 286px;

	&:hover {
		border: 3px solid var(--color-white);
		transition: border 0.1s;
		box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.35);
		text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.35);
	}
}

.order-select {
	position: relative;

	/* &:focus {
		.order-select--open {
			display: block;
		}
	} */
}

.order-select__top {
	position: relative;
	font-weight: 600;
	font-size: 18px;
	line-height: 24px;
	cursor: pointer;
	background: rgba(255, 255, 255, 0.8);
	width: 330px;
	height: 54px;
	padding: 0 25px;
	outline: none;
	padding-top: 15px;
	transition: transform 0.3s;
	user-select: none;

	border: 1px solid var(--mine-shaft);
	border-top-left-radius: 13px;
	border-top-right-radius: 13px;
	border-bottom-left-radius: 13px;
	border-bottom-right-radius: 13px;

	transition: border-bottom-color 0.3s, border-bottom-left-radius 0.3s, border-bottom-right-radius 0.3s;

	&::before {
		content: "";
		position: absolute;
		left: 9%;
		top: 50%;
		transform: translateY(-50%);
		height: 8px;
		width: 8px;
		background-image: url('../img/down.svg');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		transition: transform 0.3s;
	}
}

.order-select__dropdown {
	display: none;
	position: absolute;
	right: 0;
	width: 100%;
	z-index: 10;
	background-color: var(--color-white);

	padding-top: 10px;
	padding-bottom: 15px;
	top: 77%;

	border-bottom: 1px solid var(--mine-shaft);
    border-right: 1px solid var(--mine-shaft);
    border-left: 1px solid var(--mine-shaft);
    border-bottom-left-radius: 13px;
    border-bottom-right-radius: 13px;
}

.order-select--open {
	transition: transform 0.3s;
	.order-select__top::before {
		transform: translateY(-50%) rotate(180deg);
		transition: transform 0.3s;
	}

	.order-select__dropdown {
		display: block;
	}

	.order-select__top {
		border: 1px solid var(--mine-shaft);
		border-bottom-color: rgba(215, 215, 215, 100%);
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
		transition: border-bottom-color 0.3s, border-bottom-left-radius 0.3s, border-bottom-right-radius 0.3s;
		
	}
}

.order-select__item {
	&:not(:first-child) {
		margin-top: 6px;
	}
}

.order-checkbox {
	display: inline-block;
	cursor: pointer;
	user-select: none;
}

.order-checkbox__text {
	padding-right: 45px;
	position: relative;
	font-weight: 600;
	font-size: 18px;
	line-height: 24px;
	color: var(--mine-shaft);
	transition: opacity 0.3s;

	&::before {
		content: "";
		display: block;
		width: 14px;
		height: 14px;
		position: absolute;
		background: url('../img/checked-border.svg') no-repeat;
		background-size: 14px 14px;
		top: 7px;
		right: 17px;
		z-index: 1;
		transition: opacity 0.3s;
	}

	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 10px;
		right: 20px;
		width: 8px;
		height: 8px;
		background: url('../img/checked.svg') no-repeat;
		background-size: 8px 8px;
		opacity: 0;
		transition: opacity 0.3s;
		z-index: 2;
	}
}

.order-checkbox__input:checked + .order-checkbox__text::after {
	opacity: 1;
	transition: opacity 0.3s;
}