.main-products__container {
    padding-top: 42px;
    padding-bottom: 40px;
}

.recommendation__title {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 45px;
    
    &::before {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        background: var(--bright-red);
        right: 31%;
        top: 40%;
        z-index: -2;
        position: absolute;
    }

    &::after {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        background: var(--bright-red);
        left: 31%;
        top: 40%;
        z-index: -2;
        position: absolute;
    }
}