.partners__container {
    padding-top: 32px;
    padding-bottom: 41px;
}

.partners__title {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 42px;
    
    &::before {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        background: var(--bright-red);
        right: 32%;
        top: 38%;
        z-index: -2;
        position: absolute;
    }

    &::after {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        background: var(--bright-red);
        left: 32%;
        top: 38%;
        z-index: -2;
        position: absolute;
    }
}

.partners__img {
    display: block;
    margin: 0 auto;
}