.privileges__container {
    padding-top: 43px;
    padding-bottom: 40px;
    position: relative;
}

.privileges__title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 41px;
    position: relative;

    &::before {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        background: var(--bright-red);
        right: 32%;
        top: 38%;
        z-index: -2;
        position: absolute;
    }

    &::after {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        background: var(--bright-red);
        left: 32%;
        top: 38%;
        z-index: -2;
        position: absolute;
    }
}

.privileges__wrapper {
    display: flex;
    justify-content: space-between;
}

.privileges__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 240px;
    text-align: center;
}

.privileges__subtitle {
    text-align: center;
    font-weight: 700;
    font-size: 24px;
    margin-top: 23px;
    margin-bottom: 22px;
    color: var(--bright-red);
}

.privileges__descr {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
}