  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.35);
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.35);
}

.order-select {
  position: relative;
  /* &:focus {
		.order-select--open {
			display: block;
		}
	} */
}

.order-select__top {
  position: relative;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.8);
  width: 330px;
  height: 54px;
  padding: 0 25px;
  outline: none;
  padding-top: 15px;
  transition: transform 0.3s;
  user-select: none;
  border: 1px solid var(--mine-shaft);
  border-top-left-radius: 13px;
  border-top-right-radius: 13px;
  border-bottom-left-radius: 13px;
  border-bottom-right-radius: 13px;
  transition: border-bottom-color 0.3s, border-bottom-left-radius 0.3s, border-bottom-right-radius 0.3s;
}

.order-select__top::before {
  content: "";
  position: absolute;
  left: 9%;
  top: 50%;
  transform: translateY(-50%);
  height: 8px;
  width: 8px;
  background-image: url("../img/down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: transform 0.3s;
}

.order-select__dropdown {
  display: none;
  position: absolute;
  right: 0;
  width: 100%;
  z-index: 10;
  background-color: var(--color-white);
  padding-top: 10px;
  padding-bottom: 15px;
  top: 77%;
  border-bottom: 1px solid var(--mine-shaft);
  border-right: 1px solid var(--mine-shaft);
  border-left: 1px solid var(--mine-shaft);
  border-bottom-left-radius: 13px;
  border-bottom-right-radius: 13px;
}

.order-select--open {
  transition: transform 0.3s;
}

.order-select--open .order-select__top::before {
  transform: translateY(-50%) rotate(180deg);
  transition: transform 0.3s;
}

.order-select--open .order-select__dropdown {
  display: block;
}

.order-select--open .order-select__top {
  border: 1px solid var(--mine-shaft);
  border-bottom-color: #d7d7d7;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  transition: border-bottom-color 0.3s, border-bottom-left-radius 0.3s, border-bottom-right-radius 0.3s;
}

.order-select__item:not(:first-child) {
  margin-top: 6px;
}

.order-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;
}

.order-checkbox__text {
  padding-right: 45px;
  position: relative;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: var(--mine-shaft);
  transition: opacity 0.3s;
}

.order-checkbox__text::before {
  content: "";
  display: block;
  width: 14px;
  height: 14px;
  position: absolute;
  background: url("../img/checked-border.svg") no-repeat;
  background-size: 14px 14px;
  top: 7px;
  right: 17px;
  z-index: 1;
  transition: opacity 0.3s;
}

.order-checkbox__text::after {
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  right: 20px;
  width: 8px;
  height: 8px;
  background: url("../img/checked.svg") no-repeat;
  background-size: 8px 8px;
  opacity: 0;
  transition: opacity 0.3s;
  z-index: 2;
}

.order-checkbox__input:checked + .order-checkbox__text::after {
  opacity: 1;
  transition: opacity 0.3s;
}

