.footer__top-contacts {
  margin-left: 82px;
}

.footer__list {
  position: relative;
  font-size: 14px;
  line-height: 24px;
}

.footer__item {
  transition: opacity 0.3s;
}

.footer__item:not(:first-child) {
  margin-top: 5px;
}

.footer__item:hover {
  transition: opacity 0.3s;
  opacity: 0.7;
}

.social__list {
  position: relative;
  display: flex;
  align-items: center;
}

.social__item:not(:first-child) {
  margin-right: 25px;
}

.social__link {
  transition: opacity 0.3s;
}

.social__link svg {
  width: 24px;
  height: 24px;
}

.social__link:hover {
  transition: opacity 0.3s;
  opacity: 0.7;
}

.footer__phone {
  display: block;
  font-size: 14px;
  line-height: 24px;
  font-weight: 600;
  padding-right: 36px;
  position: relative;
  background-repeat: no-repeat;
  background-position: right center;
  background-image: url("../img/tel-footer.svg");
  transition: opacity 0.3s;
}

.footer__phone:hover {
  transition: opacity 0.3s;
  opacity: 0.7;
}

.footer__email {
  display: block;
  font-size: 14px;
  line-height: 24px;
  font-weight: 600;
  padding-right: 36px;
  position: relative;
  background-repeat: no-repeat;
  background-position: right center;
  background-image: url("../img/email-footer.svg");
  transition: opacity 0.3s;
}

.footer__email:hover {
  transition: opacity 0.3s;
  opacity: 0.7;
}

.footer__email + .footer__phone {
  padding-top: 4px;
}

.footer__bottom {
  position: relative;
  margin: 60px 0 0px;
  border-top: 2px solid rgba(208, 208, 208, 0.2);
}

.copy {
  position: relative;
  display: block;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-alto);
  padding-top: 23px;
  z-index: 1;
}

.catalog__container {
  padding-top: 42px;
}

.catalog__title {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 45px;
}

.catalog__title::before {
  content: "";
  display: block;
  width: 150px;
  height: 2px;
  color: var(--bright-red);
  right: 32%;
  top: 38%;
  z-index: -2;
  position: absolute;
}

.catalog__title::after {
  content: "";
  display: block;
  width: 150px;
  height: 2px;
  color: var(--bright-red);
  left: 32%;
  top: 38%;
  z-index: -2;
  position: absolute;
}

.card {
  padding-top: 42px;
}

.card-content .container {
  display: flex;
}

.card__left {
  width: 475px;
}

.card-info__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
  color: var(--bright-red);
  max-width: 475px;
  margin-bottom: 30px;
}

.card-info__price {
  display: inline-block;
  padding: 3px 17px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: var(--bright-red);
  border: 2px solid var(--bright-red);
  margin-bottom: 32px;
}

.card-info__price span {
  margin-left: 20px;
}

.card-info__section {
  margin-right: 21px;
}

.info__title {
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  color: var(--bright-red);
}

.characteristics__list {
  margin-top: 32px;
  margin-bottom: 32px;
  max-width: 230px;
}

.characteristics__item {
  display: flex;
  justify-content: space-between;
}

.characteristics__item:not(:last-child) {
  margin-bottom: 6px;
}

.right-info {
  width: 50%;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
}

.left-info {
  width: 50%;
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
}

.information__list {
  max-width: 350px;
}

.information__item {
  display: flex;
  justify-content: space-between;
}

.information__item:not(:last-child) {
  margin-bottom: 6px;
}

.information__notice {
  font-size: 14px;
  line-height: 14px;
  color: var(--mine-shaft);
}

.improvements {
  margin-top: 30px;
}

.improvements-list {
  margin-top: 34px;
  margin-bottom: 34px;
  max-width: 350px;
}

.improvements__item {
  display: flex;
  justify-content: space-between;
}

.improvements__item:not(:last-child) {
  margin-bottom: 6px;
}

.notice__item:not(:last-child) {
  margin-bottom: 6px;
}

.notice__text {
  font-size: 14px;
  line-height: 14px;
  color: var(--mine-shaft);
}

.card-info__btn--order {
  margin-top: 34px;
  margin-bottom: 42px;
  padding: 17px 102px;
  border: 3px solid transparent;
}

.card-info__btn--order:hover {
  box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.35);
  border-color: var(--color-white);
  transition: all 0.3s;
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.35);
}

.card__right {
  padding-right: 252px;
  width: 60%;
}

.card-main img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-privileges {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 122px;
}

.card-privileges__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 70px;
  text-align: center;
}

.card-privileges__info:nth-child(3n) {
  min-width: 100px;
}

.card-privileges__text {
  text-align: center;
  color: var(--bright-red);
  font-weight: 700;
  font-size: 18px;
  margin-top: 6px;
}

.breadcrumbs {
  display: flex;
  align-items: baseline;
  width: 100%;
  padding: 0 0 10px;
  overflow: auto;
  margin-bottom: 23px;
}

.breadcrumbs__item {
  font-size: 16px;
  line-height: 16px;
  color: var(--mine-shaft);
}

.breadcrumbs__item:not(:last-child) {
  padding-left: 15px;
}

.breadcrumbs__item + .breadcrumbs__item::before {
  content: '/';
  color: var(--mine-shaft);
}

.breadcrumbs__link {
  color: var(--mine-shaft);
  transition: opacity 0.3s;
}

.breadcrumbs__link:hover {
  opacity: 0.7;
  transition: opacity 0.3s;
}

.information__container {
  padding-top: 40px;
  padding-bottom: 42px;
}

.information__text {
  max-width: 885px;
  margin-top: 16px;
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  color: var(--mine-shaft);
}

.information__text--lots {
  max-width: 885px;
  margin-top: 16px;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: var(--mine-shaft);
}

.information h3:not(:first-child) {
  margin-top: 30px;
}

.information p {
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
}

