.contact__container {
  padding-top: 40px;
  padding-bottom: 45px;
  display: flex;
}

.contact__right {
  padding-left: 300px;
}

.contact__title {
  margin-bottom: 32px;
}

.contact__form {
  display: flex;
  flex-direction: column;
}

.contact__textarea {
  padding-top: 5px;
  resize: none;
  width: 330px;
  height: 103px;
}

.contact__btn {
  margin-top: 20px;
  height: 54px;
  width: 200px;
}

.contact-box__top {
  margin-bottom: 34px;
}

.contact__phone {
  cursor: pointer;
}

.contact__email {
  cursor: pointer;
}

.contact__social a svg {
  width: 24px;
  height: 24px;
}
