.card {
    padding-top: 42px;
}

.card-content  {
    .container {
        display: flex;
    }
}

.card__left {
    width: 475px;
}

.card-info__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    color: var(--bright-red);
    max-width: 475px;
    margin-bottom: 30px;
}

.card-info__price {
    display: inline-block;
    padding: 3px 17px;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    color: var(--bright-red);
    border: 2px solid var(--bright-red);
    margin-bottom: 32px;

    span {
        margin-left: 20px;
    }
}

.card-info__section {
    margin-right: 21px;
}

.info__title {
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
    color: var(--bright-red);
}

.characteristics__list {
    margin-top: 32px;
    margin-bottom: 32px;
    max-width: 230px;
}

.characteristics__item {
    display: flex;
    justify-content: space-between;
    
    &:not(:last-child) {
        margin-bottom: 6px;
    }
}

.right-info {
    width: 50%;
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
}

.left-info {
    width: 50%;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
}

.information__list {
    max-width: 350px;
}

.information__item {
    display: flex;
    justify-content: space-between;

    &:not(:last-child) {
        margin-bottom: 6px;
    }
}

.information__notice {
    font-size: 14px;
    line-height: 14px;
    color: var(--mine-shaft);
}

.improvements {
    margin-top: 30px
}

.improvements-list {
    margin-top: 34px;
    margin-bottom: 34px;
    max-width: 350px;
}

.improvements__item {
    display: flex;
    justify-content: space-between;

    &:not(:last-child) {
        margin-bottom: 6px;
    }
}

.notice__item {
    &:not(:last-child) {
        margin-bottom: 6px;
    }
}

.notice__text {
    font-size: 14px;
    line-height: 14px;
    color: var(--mine-shaft);
}


.card-info__btn--order {
    margin-top: 34px;
    margin-bottom: 42px;
    padding: 17px 102px;
    border: 3px solid transparent;

    &:hover {
        box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.35);
        border-color: var(--color-white);
        transition: all 0.3s;
        text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.35);
    }
}

.card__right {
    padding-right: 252px;
    width: 60%;
}

.card-main {
    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.card-privileges {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 122px;
}

.card-privileges__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 70px;
    text-align: center;

    &:nth-child(3n) {
        min-width: 100px;
    }
}

.card-privileges__text {
    text-align: center;
    color: var(--bright-red);
    font-weight: 700;
    font-size: 18px;
    margin-top: 6px;
}