.breadcrumbs {
    display: flex;
    align-items: baseline;
    width: 100%;
    padding: 0 0 10px;
    overflow: auto;
    margin-bottom: 23px;
}

.breadcrumbs__item {
    font-size: 16px;
    line-height: 16px;
    color: var(--mine-shaft);
    
    &:not(:last-child) {
        padding-left: 15px;
    }
}

.breadcrumbs__item + .breadcrumbs__item::before {
    content: '/';
    color: var(--mine-shaft);
}

.breadcrumbs__link {
    color: var(--mine-shaft);
    transition: opacity 0.3s;

    &:hover {
        opacity: 0.7;
        transition: opacity 0.3s;
    }
}