.information__container {
    padding-top: 40px;
    padding-bottom: 42px;
}

.information__text {
    max-width: 885px;
    margin-top: 16px;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    color: var(--mine-shaft);
}

.information__text--lots {
    max-width: 885px;
    margin-top: 16px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: var(--mine-shaft);
}

.information {
    h3 {
        &:not(:first-child) {
            margin-top: 30px;
        }
    }

    p {
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        // margin-top: 16px;
        // line-height: 20px;
    }
}

