.products-grid {
    display: flex;
    flex-wrap: wrap;
}

.products-grid__item {
    width: 22.5%;
}

.products-grid__item:not(:nth-child(4n)) {
    margin-right: 3.12%;
}

.products-grid__item {
    margin-bottom: 86px;

    &:nth-last-child(-n+4) {
        margin-bottom: 41px;
    }
}

.recommendation__products {
    margin-bottom: 1px !important;
}

.product {
    position: relative;
    width: 290px;
    border-radius: 8px;

    &::before {
        content: "";
        position: absolute;
        display: block;
        border: 3px solid #B10000;
        border-radius: 8px;
        transition: all 0.4s ease-in-out;
        right: 0px;
        top: 0px;
        left: 0px;
        bottom: -0px;
        border-radius: 8px;
        opacity: 0;
        visibility: hidden;
        width: 100%;
        height: 100%;
        z-index: 2;
    }

    &:hover::before {
        width: 100%;
        height: 100%;
        opacity: 1;
        visibility: visible;
        filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.35));
    }
}

.product__image {
    margin-bottom: 10px;

    img {
        object-fit: cover;
        max-height: 100%;
    }
}

.product__title {
    color: var(--bright-red);
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    padding: 0px 26px;
    margin-bottom: 14px;
}

.product__descr {
    text-align: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 20px;
    padding: 0 2px;
}

.product__bottom {
    display: flex;
    justify-content: space-between;
    border-top: 2px solid var(--bright-red);
    margin: 0 15px;
    padding-bottom: 12px;
}

.product__price {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    background-color: var(--bright-red);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-top: 6px;
    margin-left: 7px;
}

.product__link {
    background-color: var(--bright-red);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    margin-top: 10px;
    margin-right: 5px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        z-index: 3;
    }

    &:hover {
        font-weight: 700;
    }
}