.header__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 80px;
}

.header__communication {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 168px;
    width: 650px;
}

.header__item {
    &:not(:last-child) {
        margin-left: 26px;
    }
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    padding-right: 65px;
    max-width: 214px;
}

.header__item--consultation,
.header__item--delivery,
.header__item--whatsapp,
.header__item--facebook {
    display: block;
    position: relative;

    &:after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 42px;
        height: 42px;
    }
}

.header__item--consultation::after {
    background-image: url('../img/person.svg');
}

.header__item--delivery::after {
    background-image: url('../img/delivery.svg');
}

.header__item--whatsapp::after {
    background-image: url('../img/whatsapp.svg');
}

.header__item--facebook::after {
    background-image: url('../img/facebook.svg');
}

.header__phone {
    display: block;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    background-image: url('../img/tel.svg');
    background-position: right center;
    background-repeat: no-repeat;
    padding-right: 36px;
    transition: opacity 0.3s;

    &:hover {
        transition: opacity 0.3s;
        opacity: 0.7;
    }
}

.header__email {
    display: block;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    background-image: url('../img/email.svg');
    background-position: right center;
    background-repeat: no-repeat;
    padding-right: 36px;
    transition: opacity 0.3s;

    &:hover {
        transition: opacity 0.3s;
        opacity: 0.7;
    }
}

.header__bottom {
    background: linear-gradient(269.96deg, #B10000 33.85%, #F16228 67.71%, #F29221 100%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.23);
    min-height: 36px;
}

.menu {
    color: var(--color-white);
}

.menu__list {
    display: flex;
}

.menu__item {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    position: relative;
    margin-top: 4px;
    padding: 0 11px;
    border: 2px solid transparent;
    border-radius: 5px;

    &:hover {
        border: 2px solid var(--color-white);
        transition: ease-in-out 0.3s;

        filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.35));
    }

    &:active {
        border: 2px solid var(--color-white);
    }

    &:first-child {
        margin-right: 9px;
    }

    &:not(:last-child) {
        margin-left: 35px;
    }

    &::after {
        content: "";
        position: absolute;
        width: 6px;
        height: 12px;
        top: 6px;
        right: -18px;
        background-image: url('../img/line.svg');
        background-repeat: no-repeat;
        background-position: right center;
        pointer-events: none;
        z-index: 10;
    }

    &:first-child::after {
        display: none;
    }
}

.menu__item--active {
    border: 2px solid var(--color-white);
    border-radius: 5px;
    filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.35));
}

.menu__page {
    position: relative;
    z-index: 10;
}

.current-menu-item {
    border: 2px solid var(--color-white);
    border-radius: 5px;
    filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.35));
}