/* Vars */
@import "vars";

/* Fonts */
@import "mixins/font-face";
@import "fonts";

/* Mixins */
@import "mixins/visually-hidden";
@import "mixins/stop-scroll";

/* Vendor */
@import "vendor/normalize.css";
@import "vendor/modal";

html {
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

a {
    text-decoration: none;
    color: inherit;
    display: inline-block;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

p {
    margin: 0;
}

img {
    max-width: 100%;
}

button {
    border: none;
    padding: 0 ;
    background: transparent;
    cursor: pointer;
}

body {
    margin: 0;
    color: var(--color-black);
    background-color: var(--color-white);
    font-family: 'Assistant', sans-serif;
    font-weight: 400;
}

.page {
    overflow: hidden;
}

.container {
    max-width: 1300px;
    padding: 0 10px;
    margin: 0 auto;
}

.visually-hidden {
    @include visually-hidden;
}

.btn {
    background: var(--bright-red);
    border-radius: 13px;
    text-align: center;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    color: var(--color-white);

    &:hover {
        border: 3px solid var(--color-white);
        box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.35);
        text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.35);
        transition: border 0.1s, box-shadow 0.3s, text-shadow 0.3s;
    }
}

.section-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    color: var(--bright-red);
    
    span {
        color: var(--bright-red);
    }
}

/* Blocks */
@import "blocks/header";
@import "blocks/advantages";
@import "blocks/recommendation";
@import "blocks/product";
@import "blocks/consultation";
@import "blocks/privileges";
@import "blocks/partners";
@import "blocks/footer";
@import "blocks/catalog-page";
@import "blocks/card";
@import "blocks/breadcrumbs";
@import "blocks/information";
@import "blocks/contact";
