.catalog__container {
    padding-top: 42px;
}

.catalog__title {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 45px;

    &::before {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        color: var(--bright-red);
        right: 32%;
        top: 38%;
        z-index: -2;
        position: absolute;
    }

    &::after {
        content: "";
        display: block;
        width: 150px;
        height: 2px;
        color: var(--bright-red);
        left: 32%;
        top: 38%;
        z-index: -2;
        position: absolute;
    }
}