.advantages {
    background-image: url('../img/advantages-bg.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    min-height: 652px;
    background-color: #ccc;
}

.advantages__container {
    padding-top: 58px;
    padding-bottom: 40px;
}

.advantages__table {
    display: block;
    position: relative;
    z-index: 2;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right center;
    width: 501px;
    height: 161px;
    background-image: url('../img/table.svg');

    &::after {
        content: "";
        position: absolute;
        background-image: url('../img/check.svg');
        width: 79px;
        height: 77px;
        top: 24px;
        right: 133px;
    }
}

.advantages__table {
    &:not(:first-child) {
        margin-top: -6px;
    }
}

.advantages__text {
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    right: 47%;
    font-weight: 700;
    font-size: 24px;
    line-height: 26px;
    max-width: 195px;
}

.advantage__btn {
    height: 63px;
    width: 286px;
    margin: 22px 133px 0 0;
    padding: 17px 40px;
    border: 3px solid transparent;
    z-index: 4;
}